body  {
  background-color: #cccccc;
  background-image: url("./components/images/background-img.png");
  
}

h {
  font-family: 'Oswald', sans-serif;
  font-weight: bold;
  font-size: 5.9vw;
  color: black;
}
p, ul {
  font-family: 'Montserrat', sans-serif;
  font-size: 2vmin;
  color: black;
}
img {
  
  margin: 5px;
  padding: 5px;
  border: 5px;
  outline:5px;
 
}
.content-box {
  margin: 20px;
  padding: 15px;
  border:10px;

}
.resume-border-box, .portfolio-table { 
  margin: 20px;
  padding: 10px;
  border: 10px;
  box-sizing: border-box;
  background-color: whitesmoke;
  box-shadow: 10px 10px 5px black;
  text-align: relative;
}
input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=submit] {
  background-color: lightblue;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: lightblue;
}
.fab, .fas {
  color: black;
}
.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
  background: white;
  text-align: center;   
  }